import React, { useEffect, useState } from 'react';
import FormContainer from '../components/FormContainer';
import { Form, Button } from 'react-bootstrap';
import Loader from '../components/Loader';
import Message from '../components/Message';

import { useDispatch, useSelector } from 'react-redux';
import { getUserDetails, updateUserProfile } from '../actions/userActions';
import { USER_PROFILE_UPDATE_RESET } from '../constants/userConstants'

const Profile = ({location, history}) => {

    const redirect = location.search ? location.search.split('=')[1] : '/'

    const dispatch = useDispatch()

    const [formData, setFormData] = useState({
        first_name: '',
        last_name: '',
        email: '',
        password: ''
    });

    const { first_name, last_name, email, password } = formData;
    
    const onChange = e => setFormData({...formData, [e.target.name]: e.target.value});

    const onSubmit = e => {
        e.preventDefault();
        dispatch(updateUserProfile(first_name, last_name, email, password));
    };

    const userLoggedIn = useSelector(state => state.user)
    const { userInfo } = userLoggedIn

    const userDetails = useSelector(state => state.userDetails)
    const { error, user} = userDetails

    const userProfileUpdate = useSelector(state => state.userProfileUpdate)
    const { loading, success, updateError } = userProfileUpdate

    useEffect(() => {
        if(!userInfo){
            // If user not authenticated?
            // Redirect them to the login
            history.push('/login')
        }else{
            if(!user || success)
            {
                dispatch({type:USER_PROFILE_UPDATE_RESET})
                dispatch(getUserDetails())
            }
            else{
                setFormData({...formData, ['first_name']: user.first_name, ['last_name']: user.last_name, ['email']: user.email})
            }
        }
    }, [history, user, userInfo, redirect, success])

    return (
        <>
            <FormContainer>
                <h1>Sign In</h1>
                {loading ? <Loader /> : updateError ? <Message variant="danger">{updateError}</Message> : error ? <Message variant="danger">{error}</Message> : <></>}
                <Form onSubmit={onSubmit}>
                    <Form.Group controlId="first_name">
                        <Form.Label>First Name</Form.Label>
                        <Form.Control
                            type="text"
                            required
                            placeholder="first_name"
                            value={first_name}
                            name="first_name"
                            onChange={e => onChange(e)}
                        >
                        </Form.Control>
                    </Form.Group>

                    <Form.Group controlId="last_name">
                        <Form.Label>Last Name</Form.Label>
                        <Form.Control
                            type="text"
                            required
                            placeholder="last_name"
                            value={last_name}
                            name="last_name"
                            onChange={e => onChange(e)}
                        >
                        </Form.Control>
                    </Form.Group>
                    
                    <Form.Group controlId="email">
                        <Form.Label>Email</Form.Label>
                        <Form.Control
                            type="email"
                            required
                            placeholder="Email"
                            value={email}
                            name="email"
                            onChange={e => onChange(e)}
                        >
                        </Form.Control>
                    </Form.Group>

                    <Form.Group controlId="password">
                        <Form.Label>New Password</Form.Label>
                        <Form.Control
                            type="password"
                            placeholder="New Password"
                            value={password}
                            name="password"
                            onChange={e => onChange(e)}
                        >
                        </Form.Control>
                    </Form.Group>
                    <Button type="submit" variant="primary" className="my-2">update</Button>
                </Form>
            </FormContainer>
        </>
    );
};

export default Profile;
