import React from 'react'
import { Spinner } from 'react-bootstrap';
const Loader = () => {
    return (
        <Spinner
            animation="border"
            role="status"
            style={{
                position: 'absolute',
                height:'200px',
                width:'200px',
                top: '45%',
                left: '45%',
                display:'block',
            }}
        >
            <span className="sr-only"></span>
        </Spinner>
    )
}

export default Loader
