import React from 'react';
import {Card, Dropdown, Row, Col} from 'react-bootstrap';
import { Draggable } from 'react-beautiful-dnd';
import { Link } from 'react-router-dom';

const Task = ({task, index}) => {
    
    return (
        <Draggable draggableId={""+task.id} index={index}>
            {(provided, snapshot) => (                
                <Card
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}
                    ref={provided.innerRef}
                    style={{
                        display:'flex',
                        userSelect: "none",
                        borderRadius: 6,
                        margin: "0 0 8px 0",
                        minHeight: "0px",
                        backgroundColor: snapshot.isDragging
                        ? "#3c7a89"
                        : "#2e4756",
                        ...provided.draggableProps.style
                    }}
                >
                    <Row>
                        <Col
                            style={{
                                minWidth: "80%"
                            }}
                        >
                            <Card.Body>
                                <Card.Title style={{color:"white"}}>{task.title}</Card.Title>
                                <Card.Subtitle className="mb-2 text-muted">Due Date: {task.planned_end_date}</Card.Subtitle>
                                <Card.Text style={{color:"white"}}>
                                    Procedure: {task.procedure}
                                </Card.Text>
                            </Card.Body>
                        </Col>
                        <Col
                            style={{
                                minWidth: "20%",
                            }}
                        >
                            <Dropdown>
                                <Dropdown.Toggle style={{background:"transparent", boxShadow: "none", paddingTop:0, paddingBottom:0}}>
                                </Dropdown.Toggle>

                                <Dropdown.Menu>
                                    <Dropdown.Item
                                        as={Link}
                                        to={{pathname: `/card/${task.id}`}}>
                                        View
                                    </Dropdown.Item>
                                    <Dropdown.Item>Delete</Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>
                        </Col>
                    </Row>
                </Card>
            )}
        </Draggable>
    )
}

export default Task
