import axios from 'axios';
import { checkToken } from './checkToken';
import {
    USER_REQUEST,
    USER_SUCCESS,
    USER_FAIL,

    USER_LOGOUT,

    USER_DETAILS_REQUEST,
    USER_DETAILS_SUCCESS,
    USER_DETAILS_FAIL,
    USER_DETAILS_RESET,

    USER_PROFILE_UPDATE_REQUEST,
    USER_PROFILE_UPDATE_SUCCESS,
    USER_PROFILE_UPDATE_FAIL,
    USER_PROFILE_UPDATE_RESET,
} from '../constants/userConstants';

export const login = (email, password) => async (dispatch) => {

    try {
        dispatch({
            type: USER_REQUEST,
        });
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };
        const body = JSON.stringify({email, password});
        const {data} = await axios.post('/user/login/', body, config);
        dispatch({
            type: USER_SUCCESS,
            payload: data,
        });
        localStorage.setItem('userInfo', JSON.stringify(data));

    } catch (error) {
        console.log(error.reponse)
        console.log(error.response.data.details)
        console.log(error.response.data.detail)
        console.log(error.message)
        dispatch({
            type: USER_FAIL,
            payload: error.reponse && error.response.data.details
            ? error.message
            : error.response.data.detail,
        })
    }
}

export const logout = () => async (dispatch) => {
    dispatch(checkToken())
    localStorage.removeItem('userInfo')
    dispatch({
        type:USER_LOGOUT
    })
    dispatch({
        type:USER_DETAILS_RESET,
    })
}

export const register = (first_name, last_name, email, password) => async (dispatch) => {
    try {
        dispatch({
            type: USER_REQUEST,
        });
        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        };
        const body = JSON.stringify({first_name, last_name, email, password});
        const {data} = await axios.post('/user/register/', body, config);
        dispatch({
            type: USER_SUCCESS,
            payload: data,
        });
        localStorage.setItem('userInfo', JSON.stringify(data));
    } catch (error) {
        dispatch({
            type: USER_FAIL,
            payload: error.reponse && error.response.data.details
            ? error.response.data.detail
            : error.message,
        })
    }
}

export const getUserDetails = () => async (dispatch, getState) => {
    try {
        dispatch({
            type: USER_DETAILS_REQUEST,
        });

        const {
            user: { userInfo}
        } = getState()

        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.access}`,
            }
        };
        
        const {data} = await axios.get('/user/user-details/', config);

        dispatch({
            type: USER_DETAILS_SUCCESS,
            payload: data,
        });

    } catch (error) {
        dispatch({
            type: USER_DETAILS_FAIL,
            payload: error.reponse && error.response.data.details
            ? error.response.data.detail
            : error.message,
        })
    }
}

export const updateUserProfile = (first_name, last_name, email, password) => async (dispatch, getState) => {
    try {
        dispatch({
            type: USER_PROFILE_UPDATE_REQUEST,
        });

        const {
            user: { userInfo}
        } = getState()

        console.log(userInfo.access)
        
        const config = {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${userInfo.access}`,
            }
        };
        const body = JSON.stringify({first_name, last_name, email, password});
        const {data} = await axios.put('/user/update/', body, config);
        
        dispatch({
            type: USER_PROFILE_UPDATE_SUCCESS,
            payload: data,
        });
        
        dispatch({
            type: USER_SUCCESS,
            payload: data,
        });

        localStorage.setItem('userInfo', JSON.stringify(data));

    } catch (error) {
        dispatch({
            type: USER_PROFILE_UPDATE_FAIL,
            payload: error.reponse && error.response.data.details
            ? error.response.data.detail
            : error.message,
        })
    }
}