import { createEntityAdapter,  createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

export const fetchColumns = createAsyncThunk(
    'columns/fetchColumns',
    async () => {
        const {data} = await axios.get('/api/columns/');
        return data;
    }
)

const columnsAdapter = createEntityAdapter({    
    selectId: (column) => column.id
})

const columnsSlice = createSlice({
    name: 'columns',
    initialState: columnsAdapter.getInitialState({ loading: false }),
    reducers:{},
    extraReducers:{
        [fetchColumns.pending](state){
            state.loading = true
        },
        [fetchColumns.fulfilled](state, {payload}){
            state.loading = false
            columnsAdapter.setAll(state, payload)
        },
        [fetchColumns.rejected](state){
            state.loading = false
        },
    },
})

export const columnsSelectors = columnsAdapter.getSelectors((state) => state.columns);

export default columnsSlice.reducer