import React, { useState } from 'react'
import { Button, Modal, Form, Container } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Loader from '../components/Loader';
import { createWorkpackage } from './board/slice/workpackageSlice'
const NewCard = () => {
    const loading = useSelector(state => state.workpackages.loading)
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);


    const dispatch = useDispatch()
    const [formData, setFormData] = useState({
        title: '',
        procedure: '',
        planned_start_date: '',
        planned_end_date: '',
        planned_efforts: '',
    });

    const { title, procedure, planned_start_date, planned_end_date, planned_efforts } = formData;

    const onChange = e => {
        setFormData({...formData, [e.target.name]: e.target.value});
    }

    const onSubmit = e => {
        e.preventDefault();
        dispatch(createWorkpackage({ title, procedure, planned_start_date, planned_end_date, planned_efforts }))
    };

    return (
        <>
            <div className="d-grid gap-2" style={{maxWidth:"200px"}}>
                <Button className="p-1" variant="outline-dark" style={{borderStyle:"dashed", borderRadius:"20px"}} onClick={handleShow}>
                    Add card <i className="fa fa-plus" aria-hidden="true"></i>
                </Button>
            </div>
            <Modal
                show={show}
                onHide={handleClose}
                backdrop="static"
                keyboard={false}
                size="lg"
            >
                <Modal.Header closeButton>
                    <Modal.Title>New Card</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Container className="px-5">
                        {loading ? <Loader/> : <></>}
                        <Form onSubmit={onSubmit}>
                            <Form.Group controlId="title">
                                <Form.Label>Title</Form.Label>
                                <Form.Control
                                    type="text"
                                    required
                                    placeholder="title"
                                    value={title}
                                    name="title"
                                    onChange={e => onChange(e)}
                                >
                                </Form.Control>
                            </Form.Group>

                            <Form.Group controlId="procedure">
                                <Form.Label>Procedure</Form.Label>
                                <Form.Control
                                    type="text"
                                    required
                                    placeholder="procedure"
                                    value={procedure}
                                    name="procedure"
                                    onChange={e => onChange(e)}
                                >
                                </Form.Control>
                            </Form.Group>
                            
                            <Form.Group controlId="planned_start_date">
                                <Form.Label>Planned Start Date</Form.Label>
                                <Form.Control
                                    type="date"
                                    required
                                    placeholder="Email"
                                    value={planned_start_date}
                                    name="planned_start_date"
                                    onChange={e => onChange(e)}
                                >
                                </Form.Control>
                            </Form.Group>

                            <Form.Group controlId="planned_end_date">
                                <Form.Label>Planned End Date</Form.Label>
                                <Form.Control
                                    type="date"
                                    placeholder="New Password"
                                    value={planned_end_date}
                                    name="planned_end_date"
                                    onChange={e => onChange(e)}
                                >
                                </Form.Control>
                            </Form.Group>
                            <Form.Group controlId="planned_efforts">
                                <Form.Label>Planned Efforts</Form.Label>
                                <Form.Control
                                    type="number"
                                    min="1"
                                    required
                                    placeholder="planned_efforts"
                                    value={planned_efforts}
                                    name="planned_efforts"
                                    onChange={e => onChange(e)}
                                >
                                </Form.Control>
                            </Form.Group>
                            <Button type="submit" onClick={handleClose} variant="primary" className="my-2">Save</Button>
                        </Form>
                    </Container>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    )
}

export default NewCard
