import {  createEntityAdapter,  createSlice,  createAsyncThunk,} from '@reduxjs/toolkit';
import { fetchWorkpackages, updateWorkpackage } from './workpackageSlice';
import axios from 'axios';

export const fetchDocuments = createAsyncThunk(
    'documents/fetchDocuments',
    async () => {
        const {data} = await axios.get('/api/documents/');
        return data;
    }
)
export const fileUpload = createAsyncThunk(
    'documents/fileUpload',
    async ({file, description, taskId}, {dispatch} ) => {
        let body = new FormData();
        body.append('file', file);
        body.append('description', description);
        body.append('workpackage', taskId);
        const {data} = await axios.post('/api/document-upload/', body, {
            onUploadProgress: (progressEvent) => {
                const totalLength = progressEvent.lengthComputable ? progressEvent.total : progressEvent.target.getResponseHeader('content-length') || progressEvent.target.getResponseHeader('x-decompressed-content-length');
                if (totalLength !== null) {
                    dispatch(progressBarValue(Math.round( (progressEvent.loaded * 100) / totalLength )))
                }
            }
        });
        return data;
    }
)

const documentsAdapter = createEntityAdapter({
    selectId: (document) => document.id
})

const documentsSlice = createSlice({
    name: 'documents',
    initialState: documentsAdapter.getInitialState({ loading: false, processDetail:0 }),
    reducers:{
        progressBarValue(state, {payload}) {
            state.processDetail = payload
        },
    },
    extraReducers:{
        [fetchWorkpackages.fulfilled](state, {payload}){
            let byTasks={}
            for(const workpackage of payload)
            {
                byTasks[workpackage.id]=workpackage.documents
            }
            state.byTasks = byTasks
        },
        [updateWorkpackage.fulfilled](state, {payload}){
            state.byTasks[payload.id]=[]
        },

        [fetchDocuments.pending](state){
            state.loading = true
        },
        [fetchDocuments.fulfilled](state, {payload}){
            state.loading = false
            documentsAdapter.setAll(state, payload)
        },
        [fetchDocuments.rejected](state){
            state.loading = false
        },
        [fileUpload.pending](state){
            state.loading = true
        },
        [fileUpload.fulfilled](state, {payload}){
            state.loading = false
            state.processDetail = 0
            documentsAdapter.addOne(state, payload)
            if(state.byTasks[payload.workpackage] == null)
                state.byTasks[payload.workpackage] = []
            state.byTasks[payload.workpackage] = [...state.byTasks[payload.workpackage], payload.id]
        },
        [fileUpload.rejected](state){
            state.loading = false
            state.processDetail = 0
        },
    }
})
export const documentsSelectors = documentsAdapter.getSelectors((state) => state.columns);
export const { progressBarValue } = documentsSlice.actions
export default documentsSlice.reducer