import React from 'react'

const CardView = ({task}) => {

    return (
        <div>
            <div>Title: {task.title}</div>
            <div>Procedure: {task.procedure}</div>
            <div>Planned Start Date: {task.planned_start_date}</div>
            <div>Planned End Date: {task.planned_end_date}</div>
            <div>Planned Efforts: {task.planned_efforts}</div>
        </div>
    )
}

export default CardView
