import React, { useEffect } from 'react';
import { Container, Button } from 'react-bootstrap';
import Loader from '../../components/Loader';
import NewCard from '../NewCard';

import { DragDropContext } from 'react-beautiful-dnd';

import { fetchColumns, columnsSelectors } from './slice/columnsSlice'
import { fetchWorkpackages, updateColumn, updateBetweenColumns } from './slice/workpackageSlice'
import { fetchDocuments } from './slice/documentsSlice';

import { useDispatch, useSelector } from 'react-redux'

import Column from './components/Column';

const Board = () => {

    const dispatch = useDispatch();

    useEffect(()=>{
        dispatch(fetchColumns())
        dispatch(fetchWorkpackages())
        dispatch(fetchDocuments())
    }, [dispatch]);

    const columnsIds = useSelector(columnsSelectors.selectIds)
    const columns = useSelector(columnsSelectors.selectEntities)
    const loading = useSelector(state => state.columns.loading)
    const workpackages = useSelector(state => state.workpackages.entities)
    const workpackagesByColumns = useSelector(state => state.workpackages.byColumns)
    
    const onDragEnd = (result) => {
        const {destination, source, draggableId} = result
        if(!destination)
            return;
        if(destination.droppableId === source.draggableId &&
            destination.index === source.index)
            return;
        const start = source.droppableId
        const finish = destination.droppableId
        if(start === finish)
        {
            const newTasksOrder = Array.from(workpackagesByColumns[source.droppableId])
            newTasksOrder.splice(source.index, 1);
            newTasksOrder.splice(destination.index, 0, draggableId);
            if(newTasksOrder.join(",") !== workpackagesByColumns[source.droppableId].join(","))
            {
                dispatch(updateColumn({columnId: source.droppableId, data: newTasksOrder}))
            }
            else
                return;
        }
        else if(start !== finish)
        {
            const startTasks = Array.from(workpackagesByColumns[source.droppableId])
            startTasks.splice(source.index, 1);
            const finishTasks = Array.from(workpackagesByColumns[destination.droppableId])
            finishTasks.splice(destination.index, 0, draggableId);
            dispatch(updateBetweenColumns({start, startTasks, finish, finishTasks, draggableId}))
        }
    }
    
    return (
        <>
        {loading ? <Loader/> : <></>}
        <Container fluid className="mt-3">
            <h1>Kanban Board</h1>
            <hr />
        </Container>
        <Container>
            <NewCard />
        </Container>
        <Container className="mt-3" style={{display:'flex', justifyContent: 'space-around'}}>
            <DragDropContext onDragEnd={onDragEnd}>
            {columns && workpackages ?
                (columnsIds.map(id => {
                    const tasks = workpackagesByColumns[id].map(taskId => workpackages[taskId])
                    return <Column key={id} columnId={id} title={columns[id].title} tasks={tasks} />
                }))
                : <>No Columns</>
            }
            </DragDropContext>
        </Container>
        </>
    )
}

export default Board;