import {  createEntityAdapter,  createSlice,  createAsyncThunk,} from '@reduxjs/toolkit';
import { fetchColumns } from './columnsSlice';
import axios from 'axios';

export const fetchWorkpackages = createAsyncThunk(
    'workpackages/fetchWorkpackages',
    async () => {
        const {data} = await axios.get('/api/workpackages/');
        return data;
    }
)

export const createWorkpackage = createAsyncThunk(
    'workpackages/createWorkpackage',
    async ({ title, procedure, planned_start_date, planned_end_date, planned_efforts }) => {
        const body = {
            title: title,
            procedure: procedure,
            planned_start_date: planned_start_date,
            planned_end_date: planned_end_date,
            planned_efforts: planned_efforts,
            project_lead_column: 1,
        };
        const { data } = await axios.put(`/api/workpackages-create/`, body);
        return data;
    }
)

export const updateWorkpackage = createAsyncThunk(
    'workpackages/updateWorkpackage',
    async ({ id, title, procedure, planned_start_date, planned_end_date, planned_efforts }) => {
        const body = {
            id: id, 
            title: title,
            procedure: procedure,
            planned_start_date: planned_start_date,
            planned_end_date: planned_end_date,
            planned_efforts: planned_efforts
        };
        const { data } = await axios.put(`/api/workpackages-update/`, body);
        return data
    }
)

export const updateColumn = createAsyncThunk(
    'workpackages/updateColumn',
    async ({columnId, data} ) => {
        const body = {
            columnId : columnId,
            order : data,
        };
        axios.put('/api/workpackages-order-update/', body);
        return {id: columnId, data}
    }
)

export const updateBetweenColumns = createAsyncThunk(
    'workpackages/updateBetweenColumns',
    async ({start, startTasks, finish, finishTasks, draggableId}) => {
        const body = {
            columnId : finish,
            workpackageId : draggableId,
            order : finishTasks,
        };
        axios.put('/api/workpackages-column-update/', body);
        return {start, startTasks, finish, finishTasks}
    }
)


const workpackagesAdapter = createEntityAdapter({
    selectId: (workpackage) => workpackage.id
})

const workpackagesSlice = createSlice({
    name: 'workpackages',
    initialState: {},
    reducers:{},
    extraReducers:{
        [fetchColumns.fulfilled](state, {payload}){
            let byColumns={}
            for(const col of payload)
            {
                byColumns[col.id]=col.lead_column
            }
            state.byColumns = byColumns
        },
        [fetchWorkpackages.pending](state){
            state.loading = true
        },
        [fetchWorkpackages.fulfilled](state, {payload}){
            state.loading = false
            workpackagesAdapter.setAll(state, payload)
        },
        [fetchWorkpackages.rejected](state){
            state.loading = false
        },
        [createWorkpackage.pending](state){
            state.loading = true
        },
        [createWorkpackage.fulfilled](state, {payload}){
            state.loading = false
            workpackagesAdapter.addOne(state, payload)
            state.byColumns[payload.project_lead_column]=[...state.byColumns[payload.project_lead_column], payload.id]
        },
        [createWorkpackage.rejected](state){
            state.loading = false
        },
        [updateWorkpackage.pending](state){
            state.loading = true
        },
        [updateWorkpackage.fulfilled](state, {payload}){
            state.loading = false
            workpackagesAdapter.updateOne(state, {id:payload.id, changes:payload})
        },
        [updateWorkpackage.rejected](state){
            state.loading = false
        },

        //Reordering and moving
        [updateColumn.pending](state){
            state.loading = true
        },
        [updateColumn.fulfilled](state, {payload}){
            state.loading = false
            state.byColumns[payload.id] = payload.data
        },
        [updateBetweenColumns.pending](state){
            state.loading = true
        },
        [updateBetweenColumns.fulfilled](state, { payload }){
            state.loading = false
            state.byColumns[payload.start] = payload.startTasks
            state.byColumns[payload.finish] = payload.finishTasks
        },
    }
})

export default workpackagesSlice.reducer