import React, {useState} from 'react';
import { useSelector } from 'react-redux'
import { Form, Button, ProgressBar } from 'react-bootstrap'
import Loader from '../../Loader';
import { fileUpload } from '../slice/documentsSlice'
import { useDispatch } from 'react-redux'

const DocumentUpload = ({taskId}) => {
    const progressBarValue = useSelector(state => state.documents.processDetail)
    const loading = useSelector(state => state.documents.loading)
    const dispatch = useDispatch();

    const [file, setFile] = useState();
    const [formData, setFormData] = useState({description: ''});
    const { description } = formData;
    const onChange = e => setFormData({...formData, [e.target.name]: e.target.value});

    const handleFile = e =>{
        setFile(e.target.files[0]);
    }
    const handleFileUpload = e => {
        e.preventDefault();
        dispatch(fileUpload({file, description, taskId}))
    }
    return (
        <div>
            {loading ? <Loader/> : <></>}
            <Form onSubmit={handleFileUpload} style={{width:"70%"}}>
                <Form.Group controlId="description">
                    <Form.Label>Description</Form.Label>
                    <Form.Control
                        type="text"
                        required
                        placeholder="description"
                        value={description}
                        name="description"
                        onChange={e => onChange(e)}
                    >
                    </Form.Control>
                </Form.Group>
                <br/>
                <Form.Group>
                    <Form.Label>File</Form.Label>
                    <br/>
                    <Form.Control
                        type="file"
                        required
                        name="file"
                        onChange={e => handleFile(e)}
                    >
                    </Form.Control>
                </Form.Group>
                <br/>
                <Button type="submit" variant="primary" className="my-2">Upload</Button>
            </Form>
            {progressBarValue ? 
                <ProgressBar animated now={progressBarValue} label={`${progressBarValue}%`} />
                : <></>
            }
        </div>
    )
}

export default DocumentUpload
