import React, { useState } from 'react'
import { Form, Button } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux';
import Loader from '../../Loader';
import { updateWorkpackage } from '../slice/workpackageSlice'
const EditCard = ({task}) => {

    const loading = useSelector(state => state.workpackages.loading)

    const dispatch = useDispatch()
    const [formData, setFormData] = useState({
        title: task.title,
        procedure: task.procedure,
        planned_start_date: task.planned_start_date,
        planned_end_date: task.planned_end_date,
        planned_efforts: task.planned_efforts,
    });

    const { title, procedure, planned_start_date, planned_end_date, planned_efforts } = formData;

    const onChange = e => {
        setFormData({...formData, [e.target.name]: e.target.value});
    }

    const onSubmit = e => {
        e.preventDefault();
        dispatch(updateWorkpackage({ id:task.id, title, procedure, planned_start_date, planned_end_date, planned_efforts }))
    };
    return (
        <div>
            {loading ? <Loader/> : <></>}
            <Form onSubmit={onSubmit}>
                <Form.Group controlId="title">
                    <Form.Label>Title</Form.Label>
                    <Form.Control
                        type="text"
                        required
                        placeholder="title"
                        value={title}
                        name="title"
                        onChange={e => onChange(e)}
                    >
                    </Form.Control>
                </Form.Group>

                <Form.Group controlId="procedure">
                    <Form.Label>Procedure</Form.Label>
                    <Form.Control
                        type="text"
                        required
                        placeholder="procedure"
                        value={procedure}
                        name="procedure"
                        onChange={e => onChange(e)}
                    >
                    </Form.Control>
                </Form.Group>
                
                <Form.Group controlId="planned_start_date">
                    <Form.Label>Planned Start Date</Form.Label>
                    <Form.Control
                        type="date"
                        required
                        placeholder="Email"
                        value={planned_start_date}
                        name="planned_start_date"
                        onChange={e => onChange(e)}
                    >
                    </Form.Control>
                </Form.Group>

                <Form.Group controlId="planned_end_date">
                    <Form.Label>Planned End Date</Form.Label>
                    <Form.Control
                        type="date"
                        placeholder="New Password"
                        value={planned_end_date}
                        name="planned_end_date"
                        onChange={e => onChange(e)}
                    >
                    </Form.Control>
                </Form.Group>
                <Form.Group controlId="planned_efforts">
                    <Form.Label>Planned Efforts</Form.Label>
                    <Form.Control
                        type="number"
                        min="1"
                        required
                        placeholder="planned_efforts"
                        value={planned_efforts}
                        name="planned_efforts"
                        onChange={e => onChange(e)}
                    >
                    </Form.Control>
                </Form.Group>
                <Button type="submit" variant="primary" className="my-2">update</Button>
            </Form>
        </div>
    )
}

export default EditCard
