import { createStore, combineReducers, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import thunk from 'redux-thunk';
import { userReducer, userDetailsReducer, userProfileUpdateReducer } from './reducers/userReducer';
import columnsReducer from './components/board/slice/columnsSlice';
import workpackagesReducer from './components/board/slice/workpackageSlice';
import documentsSlice from './components/board/slice/documentsSlice';
const reducers = combineReducers({
    user : userReducer,
    userDetails: userDetailsReducer,
    userProfileUpdate: userProfileUpdateReducer,
    columns: columnsReducer,
    workpackages: workpackagesReducer,
    documents: documentsSlice,
})

const userInfoFromStorage = localStorage.getItem('userInfo') ?
    JSON.parse(localStorage.getItem('userInfo')) : null

const initialState = {
    user: {userInfo: userInfoFromStorage},
};

const middleware = [thunk];

const store = createStore(
    reducers,
    initialState,
    composeWithDevTools(applyMiddleware(...middleware))
);

export default store;