import React from 'react';
import { LinkContainer } from 'react-router-bootstrap';
import { useDispatch, useSelector } from 'react-redux';

import { Navbar, Nav, Container, NavDropdown } from 'react-bootstrap';

import { logout } from '../actions/userActions';
const Header = () => {

    const dispatch = useDispatch()

    //Logout
    const logout_user = () => {
        dispatch(logout())
    };

    //User is Logged in
    const user = useSelector(state => state.user)
    const { userInfo } = user
    
    return (
        <header>
            <Navbar bg="dark" variant="dark" expand="lg" collapseOnSelect>
                <Container fluid>
                    <LinkContainer to="/">
                        <Navbar.Brand>Rope</Navbar.Brand>
                    </LinkContainer>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <LinkContainer to="/" className="mr-auto">
                            <Nav.Link>Home</Nav.Link>
                        </LinkContainer>
                        {userInfo ? (
                                <>
                                <LinkContainer to="/board">
                                    <Nav.Link>Board</Nav.Link>
                                </LinkContainer>
                                <NavDropdown title={userInfo.first_name+" "+userInfo.last_name} id='username'>
                                    <LinkContainer to="/profile">
                                        <NavDropdown.Item>Profile</NavDropdown.Item>
                                    </LinkContainer>
                                    <NavDropdown.Item onClick={logout_user}>Logout</NavDropdown.Item>
                                </NavDropdown>
                                </>
                            )
                            :
                            (
                                <>
                                    <LinkContainer to="/login">
                                        <Nav.Link>Login</Nav.Link>
                                    </LinkContainer>

                                    <LinkContainer to="/register">
                                        <Nav.Link>Register</Nav.Link>
                                    </LinkContainer>
                                </>
                            )
                        }
                    </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </header>
    )
}

export default Header;