import React from 'react'
import { Tab, Nav, Col, Row } from 'react-bootstrap'
import DocumentUpload from './DocumentUpload'
import DocumentsView from './DocumentsView'
import CardView from './CardView'
import EditCard from './EditCard'
const Sidebar = ({task}) => {
    return (        
        <Tab.Container defaultActiveKey="view">
        <Row>
          <Col sm={3}>
            <Nav variant="pills" className="flex-column">
              <Nav.Item>
                <Nav.Link eventKey="view">View</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="edit">Edit</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="documents">Documents</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="documentsUpload">Upload Documents</Nav.Link>
              </Nav.Item>
            </Nav>
          </Col>
          <Col sm={9}>
            <Tab.Content>
              <Tab.Pane eventKey="view">
                <CardView task={task}/>
              </Tab.Pane>
              <Tab.Pane eventKey="edit">
                <EditCard task={task}/>
              </Tab.Pane>
              <Tab.Pane eventKey="documents">
                <DocumentsView taskId={task.id}/>
              </Tab.Pane>
              <Tab.Pane eventKey="documentsUpload">
                <DocumentUpload taskId={task.id}/>
              </Tab.Pane>
            </Tab.Content>
          </Col>
        </Row>
      </Tab.Container>
    )
}

export default Sidebar
