import axios from 'axios';
import jwt_decode from "jwt-decode";

export const checkToken = () => async (dispatch, getState) => {
    const {
        user: { userInfo}
    } = getState()
    const decodedAccessToken=jwt_decode(userInfo.access, {complete: true});
    const decodedRefreshToken=jwt_decode(userInfo.refresh, {complete: true});
    const currentDate = new Date();
    if (decodedAccessToken.exp * 1000 < currentDate.getTime())
    {
        console.log("Access Token expired.");

        if (decodedRefreshToken.exp * 1000 > currentDate.getTime())
        {
            const config = {
                headers: {
                    'Content-Type': 'application/json',
                }
            };
            
            const body = { 'refresh': userInfo.refresh};
            const {data} = await axios.post('/user/token/refresh/', body, config);
            userInfo.refresh = data.refresh;
            userInfo.access = data.access;
            localStorage.setItem('userInfo', JSON.stringify(userInfo));
        }
        else
        {
            console.log('Refresh token is expired');
        }
    }
    else {
        console.log("Valid token");
    }
}