import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import { Table } from 'react-bootstrap'

const DocumentsView = ({taskId}) => {
    const documents = useSelector(state => state.documents.entities)
    const documentIds = useSelector(state => state.documents.byTasks[taskId])
    const [documentsData, setDocumentsData] = useState(documents)
    const [documentDataIds, setDocumentDataIds] = useState(documentIds)
    useEffect(() => {
        setDocumentsData(documents) 
        setDocumentDataIds(documentIds)
    }, [documents, documentIds])

    return (
        <Table striped bordered hover responsive size="sm">
            <thead>
                <tr>
                    <th><h5>Description</h5></th>
                    <th><h5>URL</h5></th>
                </tr>
            </thead>
            <tbody>
            {documentsData && documentDataIds ?
                (documentDataIds.map(docId => {
                    const docs =  documentsData[docId]
                    return (
                        <tr key={docs.id}>
                            <td>{docs.description}</td>
                            <td style={{overflowX:'scroll', overflowY: 'scroll'}}><a href={docs.file} target="_blank">{docs.file}</a></td>
                        </tr>
                    )
                }))
                : 
                <>
                    <tr>
                        <td>No Documents</td>
                        <td style={{overflowX:'scroll', overflowY: 'scroll'}}>-</td>
                    </tr>
                </>
            }
            </tbody>
        </Table>
    )
}

export default DocumentsView
